import { Injectable } from '@angular/core';
import {Observable, Subject} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class MatrixService {

  public matrix = [[{value1: 0, value2: 0, highlighted: false, removed: false}]];

  public OnChange: Subject<any>;

  public playerOneCount: number;
  public playerTwoCount: number;

  public minValue: number;
  public maxValue: number;

  constructor() {
    this.playerOneCount = 0;
    this.playerTwoCount = 0;
    this.OnChange = new Subject<any>();
  }

  initializeArray(playerOneCount, playerTwoCount): void {
    this.playerOneCount = playerOneCount;
    this.playerTwoCount = playerTwoCount;
    this.matrix = new Array(playerOneCount).fill(0).map(x => new Array(playerTwoCount).fill(0).map(xd => {
      return { value1: 0, value2: 0, highlighted: false, removed: false};
    }));
    setTimeout(x => {
      this.OnChange.next(1);
    }, 0);
  }

  initializeValues(from, to): void {
    this.minValue = from;
    this.maxValue = to;
    this.matrix.forEach(row => {
      row.forEach(col => {
        col.value1 = this.getRandomInt(from, to);
        col.value2 = this.getRandomInt(from, to);
        col.highlighted = false;
      });
    })
    this.OnChange.next(2);
  }

  getRandomInt(min, max) {
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  matrixChanged() {
    this.OnChange.next(2);
  }
}
