import { Component, OnInit } from '@angular/core';
import {MatrixService} from '../matrix.service';

@Component({
  selector: 'app-matrix-reaction-function',
  templateUrl: './matrix-reaction-function.component.html',
  styleUrls: ['./matrix-reaction-function.component.css']
})
export class MatrixReactionFunctionComponent implements OnInit {

  private matrix1: any;
  private matrix2: any;

  constructor(private matrix: MatrixService) {
    matrix.OnChange.subscribe(x => {
      this.matrix1 = JSON.parse(JSON.stringify(matrix.matrix));
      this.matrix2 = JSON.parse(JSON.stringify(matrix.matrix));

      if (x === 2) {
        this.matrix1.forEach(x => {
          let maxVal = this.matrix.minValue - 1;
          x.forEach(val => {
            maxVal = Math.max(maxVal, val.value1);
          });
          x.filter(xc => xc.value1 === maxVal).forEach(dat => {
            dat.highlighted = true;
          });
        });
        for(let i=0; i < this.matrix2[0].length; i++) {
          let maxVal = this.matrix.minValue - 1;
          for(let j=0; j < this.matrix2.length; j++) {
            maxVal = Math.max(this.matrix2[j][i].value2, maxVal);
          }
          for(let j=0; j < this.matrix2.length; j++) {
            if(this.matrix2[j][i].value2 === maxVal)
              this.matrix2[j][i].highlighted = true;
          }
        }
      }
    });
  }

  ngOnInit() {
  }

  onChanged(event) {
    console.log(event);
  }
}
