import { Component, OnInit } from '@angular/core';
import {MatrixService} from '../matrix.service';

@Component({
  selector: 'app-matrix-user-solver',
  templateUrl: './matrix-user-solver.component.html',
  styleUrls: ['./matrix-user-solver.component.css']
})
export class MatrixUserSolverComponent implements OnInit {

  matrixSteps: any = [];

  matrix: any = [[0]];

  constructor(private matrixService: MatrixService) {
    matrixService.OnChange.subscribe(x => {
      this.matrix = JSON.parse(JSON.stringify(matrixService.matrix));
      this.matrixSteps = [];
    })
  }

  ngOnInit() {
  }

  onChanged(event) {
    console.log(event);
  }

  makeCopyToSteps() {
    this.matrixSteps.push(this.matrix);
    return JSON.parse(JSON.stringify(this.matrix));
  }

  rowClicked(i) {
    if (this.matrix.length === 1) return;
    this.matrix[i].forEach(x => x.removed = true);
    this.matrix = this.makeCopyToSteps();
    this.matrix.splice(i, 1);
  }

  colClicked(i) {
    if (this.matrix[0].length === 1) return;
    this.matrix.forEach(x => {
      x[i].removed = true;
    })
    this.matrix = this.makeCopyToSteps();
    this.matrix.forEach(x => {
      x.splice(i, 1);
    })
  }

}
