import { Component, OnInit } from '@angular/core';
import {MatrixService} from '../matrix.service';

@Component({
  selector: 'app-matrix-auto-solver',
  templateUrl: './matrix-auto-solver.component.html',
  styleUrls: ['./matrix-auto-solver.component.css']
})
export class MatrixAutoSolverComponent implements OnInit {

  matrixSteps: any = [];

  matrix: any = [[0]];

  constructor(private matrixService: MatrixService) {
    matrixService.OnChange.subscribe(x => {
      this.matrix = JSON.parse(JSON.stringify(matrixService.matrix));
      this.matrixSteps = [];
    });
  }

  ngOnInit() {
  }


  addToHistory(matrix) {
    this.matrixSteps.push(JSON.parse(JSON.stringify(matrix)));
  }


  rowClicked(i) {
    this.matrix.splice(i, 1);
  }

  colClicked(i) {
    this.matrix.forEach(x => {
      x.splice(i, 1);
    });
  }

  Solve() {
    let ok = true;

    while (ok) {
      ok = false;
      for (let x = 0; x < this.matrix.length; x++) {
        for (let y = x + 1; y < this.matrix.length; y++) {
          let b1 = false, b2 = false;
          for (let j = 0; j < this.matrix[x].length; j++) {
            if (this.matrix[x][j].value1 < this.matrix[y][j].value1) {
              b1 = true;
            } else if (this.matrix[x][j].value1 > this.matrix[y][j].value1) {
              b2 = true;
            }
          }
          if (b1 && !b2) {

            this.matrix[x].forEach(xd => xd.removed = true);
            this.matrix[y].forEach(xd => xd.highlighted = true);
            this.addToHistory(this.matrix);
            this.rowClicked(x);
          } else if (b2 && !b1) {
            this.matrix[y].forEach(xd => xd.removed = true);
            this.matrix[x].forEach(xd => xd.highlighted = true);
            this.addToHistory(this.matrix);
            this.rowClicked(y);
          }
          if (b1 != b2) {
            ok = true;
            this.matrix.forEach(row => {
              row.forEach(col => {
                col.highlighted = false;
                col.removed = false;
              });
            });
          }
        }
      }
      for (let x = 0; x < this.matrix[0].length; x++) {
        for (let y = x + 1; y < this.matrix[0].length; y++) {
          let b1 = false, b2 = false;
          for (let j = 0; j < this.matrix.length; j++) {
            if (this.matrix[j][x].value2 < this.matrix[j][y].value2) {
              b1 = true;
            } else if (this.matrix[j][x].value2 > this.matrix[j][y].value2) {
              b2 = true;
            }
          }
          if (b1 && !b2) {

            this.matrix.forEach(xd => xd[x].removed = true);
            this.matrix.forEach(xd => xd[y].highlighted = true);
            this.addToHistory(this.matrix);
            this.colClicked(x);
          } else if (b2 && !b1) {
            this.matrix.forEach(xd => xd[y].removed = true);
            this.matrix.forEach(xd => xd[x].highlighted = true);
            this.addToHistory(this.matrix);
            this.colClicked(y);
          }
          if (b1 !== b2) {
            ok = true;
            this.matrix.forEach(row => {
              row.forEach(col => {
                col.highlighted = false;
                col.removed = false;
              });
            });
          }
        }
      }
    }
    this.addToHistory(this.matrix);
  }

}
