import { Component } from '@angular/core';
import {MatrixService} from './matrix.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  playerOneCount: number;
  playerTwoCount: number;

  numbersMinimum : number;
  numbersMaximum : number;

  constructor(private matrix: MatrixService) {
    this.playerOneCount = 5;
    this.playerTwoCount = 5;

    this.numbersMinimum = -10;
    this.numbersMaximum = 10;
  }

  generateMatrix() {
    this.matrix.initializeArray(this.playerOneCount, this.playerTwoCount);
  }

  generateValues() {
    if (this.matrix.playerOneCount === 0 || this.matrix.playerTwoCount === 0) { return; }
    this.matrix.initializeValues(this.numbersMinimum, this.numbersMaximum);
  }

}
